body {
    font-family: 'Manrope', sans-serif;
    font-weight: 400;
    background: -webkit-gradient(linear, left top, left bottom, from(#183D58), color-stop(22.13%, #111111));
    background: -o-linear-gradient(top, #183D58 0%, #111111 22.13%);
    background: linear-gradient(180deg, #183D58 0%, #111111 22.13%);
    min-height: 100vh;
    position: relative;
}

body::before {
    content: '';
    width: 100%;
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    height: 350px;
    background: url("../img/header-bg.svg") center bottom no-repeat;
    background-size: cover;
    z-index: 1;
}

.container {
    max-width: 1080px;
    width: 100%;
    margin: 0 auto;
}

.btn {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-negative: 0;
    flex-shrink: 0;
}

.btn-connect {
    background: #D35626;
    border-radius: 8px;
    font-size: 14px;
    color: #fff;
    width: 180px;
    height: 40px;
}

.btn-inventory {
    background: #6DEB47;
    border-radius: 39px;
    height: 55px;
    width: 210px;
    font-size: 14px;
    color: #000;
    font-family: 'Press Start 2P', cursive;
    font-weight: 400;
}

@media screen and (max-width: 991px) {
    .product-grid {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-auto-rows: auto;
    }
}

@media screen and (max-width: 768px) {
    .header {
        padding-top: 16px;
    }
    .header-wrapper {
        height: 38px;
        padding: 0 5px;
    }
    .header-social__icon {
        width: 26px;
        height: 26px;
        margin-right: 8px;
    }
    .header__profile {
        margin-right: 8px;
    }
    .inventory-wrapper__btn {
        display: none;
    }
    .inventory-wrapper__icon {
        display: block;
    }
    .btn-connect {
        height: 38px;
        width: 150px;
    }
    .page-logo {
        padding: 66px 42px 0 42px;
    }
}

@media screen and (max-width: 475px) {
    .header-wrapper {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: space-between;
    }
    .profile__img {
        width: 26px;
        height: 26px;
    }
    .language-change__current {
        height: 16px;
    }
    .language-change svg {
        width: 16px;
        height: 16px;
    }
    .page-content__wrapper {
        padding: 40px 0 80px 0;
    }
    .page-logo__text {
        margin-top: 55px;
    }
    .product-grid {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-auto-rows: 464px;
        row-gap: 45px;
        -webkit-column-gap: 4px;
        -moz-column-gap: 4px;
        column-gap: 4px;
    }
    .product-item {
        -webkit-box-shadow: 0px 0px 16px rgba(79, 126, 159, 0.7);
        box-shadow: 0px 0px 16px rgba(79, 126, 159, 0.7);
        border-radius: 8px;
        padding: 32px 4px;
        -webkit-box-pack: start;
        -ms-flex-pack: start;
        justify-content: flex-start;
    }
    .product-item__btn {
        bottom: -23px;
        width: 104px;
        margin-left: -52px;
    }
    .product-item__img {
        width: 80px;
        height: 80px;
    }
    .product-item__img img {
        width: 100%;
        height: 100%;
        -o-object-fit: contain;
        object-fit: contain;
    }
}