.social-icon {
    width: 42px;
    height: 42px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
}

.social-icon svg {
    width: 100%;
    height: 100%;
}

.profile__img {
    display: block;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    overflow: hidden;
}

.profile__img img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
}

.language-change {
    position: relative;
}

.language-change.open .language-change__list {
    z-index: 99;
    opacity: 1;
}

.language-change.open .language-change__current::after {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}

.language-change__list {
    position: absolute;
    top: 110%;
    left: 0;
    z-index: -1;
    opacity: 0;
    -webkit-transition: .3s z-index, .3s opacity;
    -o-transition: .3s z-index, .3s opacity;
    transition: .3s z-index, .3s opacity;
}

.language-change__current {
    position: relative;
    height: 20px;
}

.language-change__current::after {
    content: '';
    position: absolute;
    top: 10px;
    margin-top: -5px;
    right: -14px;
    width: 10px;
    height: 10px;
    display: block;
    background: url("../img/caret-down.svg") center center no-repeat;
    background-size: contain;
    -webkit-transition: .3s linear;
    -o-transition: .3s linear;
    transition: .3s linear;
}

.language-change__current:hover {
    cursor: pointer;
}

.language-change svg {
    width: 20px;
    height: 20px;
}

.web3modal-modal-lightbox {
    z-index: 5;
    position: relative;
}

.header {
    padding-top: 14px;
    position: relative;
    z-index: 2;
}

.header-wrapper {
    max-width: 1170px;
    width: 100%;
    margin: 0 auto;
    height: 55px;
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
}

.header-wrapper__block {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.header-wrapper__block_connect {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.header-wrapper__block_inventory {
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
}

.header-social__icon {
    margin-right: 35px;
}

.header__profile {
    margin-right: 40px;
}

.inventory-wrapper__icon {
    width: 24px;
    height: 24px;
    display: none;
}

.inventory-wrapper__icon img {
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
    object-fit: contain;
}
