.product-item {
    background: #000000;
    border-radius: 30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 30px 37px 20px 37px;
    position: relative;
}

.product-item__title {
    font-size: 25px;
    font-weight: 700;
    text-align: center;
    color: #02EF02;
}

.product-item__desc {
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    color: #fff;
    text-align: center;
    margin-top: 6px;
}

.product-item__price {
    font-weight: 600;
    font-size: 18px;
    text-align: center;
    color: #4D7A7B;
    margin-top: auto;
}

.product-item__count {
    font-size: 13px;
    color: #ffffff;
}

.product-item__img {
    height: 80px;
    width: 80px;
}

.product-item__img img {
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
    object-fit: contain;
}

.product-item__btn {
    position: absolute;
    bottom: -29px;
    left: 50%;
    margin-left: -105px;
    width: 210px;
    height: 46px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    background: #6EEC47;
    border-radius: 40px;
    font-weight: 700;
    font-size: 14px;
    color: #000;
    font-family: 'Press Start 2P', cursive;
}

.product-item__btn_sold {
    background: #838F97;
    border-radius: 11px;
    font-family: 'Manrope', sans-serif;
    pointer-events: none;
}