*,
*::after,
*::before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
    margin: 0;
}

body {
    font-family: 'HelveticaNeue';
    font-weight: 400;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    overflow-x: hidden;
    height: 100%;
}

ol,
ul {
    padding: 0;
    list-style: none;
}

a {
    font-family: inherit;
    text-decoration: none;
    color: inherit;
}

img {
    max-width: 100%;
    height: auto;
}

button {
    cursor: pointer;
    border: none;
}

button,
input,
select,
textarea {
    font-family: inherit;
}

* {
    margin: 0;
    padding: 0;
}

@font-face {
    font-family: 'Manrope';
    src: url("./fonts/Manrope-Bold/Manrope-Bold.woff2") format("woff2"), url("./fonts/Manrope-Bold/Manrope-Bold.woff") format("woff");
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Manrope';
    src: url("./fonts/Manrope-Regular/Manrope-Regular.woff2") format("woff2"), url("./fonts/Manrope-Regular/Manrope-Regular.woff") format("woff");
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Press Start 2P';
    src: url("./fonts/PressStart2P-Regular/PressStart2P-Regular.woff2") format("woff2"), url("./fonts/PressStart2P-Regular/PressStart2P-Regular.woff") format("woff");
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
